<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <Floornums @tableShow="tableShow" v-if="!tableshow"></Floornums>
      <section class="tables" v-else-if="tableshow">
        <Tables :line="tablenum*1" @tableValue="tableValue" :type="type"></Tables>
        <div class="floor_btn" v-if="!tablebtn">
          <van-button @click="onClearNum()" color="#7e7e7e">{{languages.a15[langIndex]}}</van-button>
          <van-button @click="Comfirm()" color="#dc0000">{{languages.a8[langIndex]}}</van-button>
        </div>
        <div class="floor_btn onfloor" v-else-if="tablebtn">
          <van-button
            v-if="!namebool"
            @click="onClearNum()"
            color="#7e7e7e"
          >{{languages.a15[langIndex]}}</van-button>
          <van-button v-else @click="onDone()" color="#dc0000">{{languages.a26[langIndex]}}</van-button>
        </div>
      </section>
    </main>
  </div>
</template>

<style scoped>
main {
  width: 100%;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.floor_num {
  margin-top: 2rem;
}
.floor_btn {
  /* width: 100%; */
  margin-top: 1rem;
  padding: 0 0.8rem 0 0.8rem;
  display: flex;
  justify-content: space-between;
}
.onfloor {
  justify-content: center;
}
.floor_btn .van-button {
  width: 1.8rem;
  height: 0.8rem;
}
.tables {
  margin-top: 1rem;
  margin-bottom: 1.2rem;
}
</style>

<script>
import Heads from "@/components/Head";
import Floornums from "@/components/Floornum";
import Tables from "@/components/Table";
import { Dialog } from "vant";
import arrange from "@/model/arrange";
// import { Encrypt } from "@/model/utils.js";

export default {
  name: "Lop",
  components: { Heads, Floornums, Tables },
  data() {
    return {
      tableshow: false,
      tablebtn: false,
      type: "input",
      namebool: false // 判断名字是否映射完成
    };
  },
  methods: {
    // 清空数据
    onClearNum() {
      Dialog.alert({
        showCancelButton: true,
        cancelButtonText: this.languages.a23[this.langIndex],
        confirmButtonText: this.languages.a8[this.langIndex],
        message: this.languages.a27[this.langIndex]
      })
        .then(() => {
          this.tablenum = null; // 清空楼层总数
          this.tableshow = false; // 返回
          this.type = "input";
          this.tablebtn = false;
          this.$store.commit("getTabledata", null); // 清空table数据
          this.$store.commit("getFloorbool", null); // 清空判断数据
          this.$store.commit("getFloormap", null); // 清空floorname
          this.$store.commit("getTablenum", null); // 清空表格数量
        })
        .catch(() => {});
    },
    // 确认
    Comfirm() {
      if (this.tabledata === null) {
        Dialog.alert({
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a21[this.langIndex]
        });
        return;
      }
      let floorarr = Object.values(this.tabledata);
      let nonull = floorarr.every(ele => ele != null); // 判断楼层名是否有空
      if (!nonull) {
        Dialog.alert({
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a22[this.langIndex]
        });
        return;
      }
      let arr = arrange(floorarr);
      let str = "";
      arr.forEach(ele => {
        if (ele[1]) {
          let continuity = `${ele[0]}~${ele[ele.length - 1]};`;
          str = str.concat(continuity);
        } else {
          let onestr = `${ele[0]};`;
          str = str.concat(onestr);
        }
      });
      str = str.slice(0, -1);
      if (new Set(floorarr).size != floorarr.length) {
        Dialog.alert({
          showCancelButton: true,
          cancelButtonText: this.languages.a23[this.langIndex],
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a24[this.langIndex]
        })
          .then(() => {
            this.$store.commit("getFloormap", str);
            this.type = null;
            this.tablebtn = true;
          })
          .catch(() => {});
      } else {
        this.$store.commit("getFloormap", str);
        this.type = null;
        this.tablebtn = true;
      }
    },
    onDone() {
      this.$store.commit("getTabledata", null); // 清空table数据
      this.$store.commit("getFloorbool", null); // 清空判断数据
      this.$store.commit("getFloormap", null); // 清空floorname
      this.$store.commit("getTablenum", null); // 清空表格数量
      this.$router.push({
        name: "Configure",
      });
    },
    // 接收子组件的数据
    tableValue(val) {
      this.$store.commit("getTabledata", val);
    },
    tableShow(val) {
      this.tableshow = val;
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    },
    tablenum: {
      get() {
        return this.$store.state.tablenum;
      },
      set(val) {
        this.$store.commit("getTablenum", val);
      }
    },
    tabledata() {
      return this.$store.state.tabledata;
    },
    floormap() {
      return this.$store.state.floormap;
    },
    floorbool() {
      return this.$store.state.floorbool;
    }
  },
  mounted() {
    if (this.tablenum) {
      this.tableshow = true;
    }
    if (this.tabledata) {
      this.tablebtn = true;
      this.type = null;
    }
    let bool = false;
    for (const key in this.floorbool) {
      if (this.floorbool[key] == true) {
        bool = true;
      } else {
        bool = false;
        break;
      }
    }
    this.namebool = bool;
    console.log(bool);
  }
};
</script>