<template>
  <div>
    <section class="floor_num">
      <van-field
        class="floor_input"
        v-model="tablenum"
        type="number"
        :placeholder="languages.a14[langIndex]"
        :error-message="error"
        input-align="center"
      />
      <div class="floor_btn">
        <van-button @click="returns()" color="#7e7e7e">{{languages.a15[langIndex]}}</van-button>
        <van-button @click="onFloorNum()" color="#dc0000">{{languages.a8[langIndex]}}</van-button>
      </div>
    </section>
  </div>
</template>

<style>
.floor_input .van-field__control {
  border: 0.01rem solid black;
  width: 5.4rem;
  height: 1rem;
  font-size: 0.4rem;
}
</style>

<style scoped>
.floor_num {
  margin-top: 2rem;
}
.floor_btn {
  /* width: 100%; */
  margin-top: 1rem;
  padding: 0 0.8rem 0 0.8rem;
  display: flex;
  justify-content: space-between;
}
.floor_btn .van-button {
  width: 1.8rem;
  height: 0.8rem;
}
</style>

<script>
export default {
  name: "Floornums",
  props: [],
  data() {
    return {
      error: null
    };
  },
  methods: {
    returns() {
      this.$router.back();
    },
    onFloorNum() {
      if (!this.tablenum) {
        this.error = this.languages.a16[this.langIndex];
        return;
      }
      if (this.tablenum == 0) {
        this.error = this.languages.a17[this.langIndex];
        return;
      }
      this.$emit("tableShow", true);
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    },
    tablenum: {
      get() {
        return this.$store.state.tablenum;
      },
      set(val) {
        this.$store.commit("getTablenum", val);
      }
    }
  }
};
</script>